.container {
    height: 60px;
    position: fixed;
    top: 0;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--primary-purple);
    width: 100%;
}


.nav_container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 50%;
    color: var(--primary-bg);
    font-size: 22px;
    font-weight: 900;
    cursor: pointer;
}